@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.5.0/font/bootstrap-icons.css);
@import url(https://fonts.googleapis.com/css2?family=Titan+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  position: relative;
  background: linear-gradient(45deg, rgb(10, 32, 56) 50%, rgb(35, 49, 78) 50%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Member_Left {
  position: absolute;
  left: 10px;
  top: 10px;
}

.MemberNo_Left {
  font-weight: bold;
}

.Member_Right {
  position: absolute;
  right: 10px;
  bottom: 10px;
}

.divMainGame {
  width: 60vmin;
  height: 60vmin;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  background: rgb(8, 22, 37);
  border-radius: 15px;
  z-index: 20;
  position: relative;
}

.btnReplay {
  position: absolute;
  bottom: 0;
  left: 50%;
  background: tomato;
  padding: 5px 20px 5px 23px;
  -webkit-transform: translate(-50%, 120%);
          transform: translate(-50%, 120%);
  letter-spacing: 3px;
  font-family: "Titan One", cursive;
  cursor: pointer;
}

.divSelectZone {
  width: 31%;
  height: 31%;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 10px;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  border-radius: 15px;
  font-size: 5rem;
  font-weight: bold;
}

.Color-X {
  color: rgb(255, 217, 0);
}

.Color-O {
  color: rgb(190, 56, 56);
}

.HintFontColor {
  color: rgba(255, 255, 255, 0.5) !important;
}

.arrow_left {
  margin-left: 15px;
  -webkit-animation: arrow_left 500ms infinite none ease-out alternate;
          animation: arrow_left 500ms infinite none ease-out alternate;
}

.arrow_right {
  margin-right: 15px;
  display: inline-block;
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
  -webkit-animation: arrow_right 500ms infinite none ease-out alternate;
          animation: arrow_right 500ms infinite none ease-out alternate;
}

.bi-trophy-fill {
  margin: 0 20px;
  display: inline-block;
  -webkit-animation: showUp 500ms none ease-out alternate;
          animation: showUp 500ms none ease-out alternate;
}

.visible-hidden {
  display: none !important;
}

.FlashAnimation {
  -webkit-animation: Flash 600ms 2 ease-out forwards;
          animation: Flash 600ms 2 ease-out forwards;
}

.Member_Left_Win {
  position: absolute;
  font-size: 15rem;
  font-weight: bolder;
  left: 5%;
  top: 50%;
  opacity: 0.3;
  z-index: 10;
  -webkit-animation: showUp_WIN 600ms ease-out forwards;
          animation: showUp_WIN 600ms ease-out forwards;
}

.Member_Right_Win {
  position: absolute;
  font-size: 15rem;
  font-weight: bolder;
  right: 5%;
  top: 50%;
  opacity: 0.3;
  z-index: 10;
  -webkit-animation: showUp_WIN 1000ms ease-out forwards;
          animation: showUp_WIN 1000ms ease-out forwards;
}

@-webkit-keyframes arrow_left {
  from {
    margin-left: 15px;
  }
  to {
    margin-left: 20px;
    opacity: 0.7;
  }
}

@keyframes arrow_left {
  from {
    margin-left: 15px;
  }
  to {
    margin-left: 20px;
    opacity: 0.7;
  }
}

@-webkit-keyframes arrow_right {
  from {
    margin-right: 15px;
  }
  to {
    margin-right: 20px;
    opacity: 0.7;
  }
}

@keyframes arrow_right {
  from {
    margin-right: 15px;
  }
  to {
    margin-right: 20px;
    opacity: 0.7;
  }
}

@-webkit-keyframes showUp {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes showUp {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes showUp_WIN {
  from {
    -webkit-transform: translate(0%, -50%) scale(0);
            transform: translate(0%, -50%) scale(0);
  }
  80% {
    -webkit-transform: translate(0%, -50%) scale(1);
            transform: translate(0%, -50%) scale(1);
    opacity: 0;
  }
  90% {
    opacity: 1;
  }
  to {
    -webkit-transform: translate(0%, -50%) scale(1);
            transform: translate(0%, -50%) scale(1);
  }
}

@keyframes showUp_WIN {
  from {
    -webkit-transform: translate(0%, -50%) scale(0);
            transform: translate(0%, -50%) scale(0);
  }
  80% {
    -webkit-transform: translate(0%, -50%) scale(1);
            transform: translate(0%, -50%) scale(1);
    opacity: 0;
  }
  90% {
    opacity: 1;
  }
  to {
    -webkit-transform: translate(0%, -50%) scale(1);
            transform: translate(0%, -50%) scale(1);
  }
}

@-webkit-keyframes Flash {
  0%,
  25%,
  75% {
    background: rgba(255, 255, 255, 0.5);
  }
  50%,
  100% {
    background: rgba(255, 255, 255, 0.9);
  }
}

@keyframes Flash {
  0%,
  25%,
  75% {
    background: rgba(255, 255, 255, 0.5);
  }
  50%,
  100% {
    background: rgba(255, 255, 255, 0.9);
  }
}

@media screen and (orientation: portrait) {
  .divSelectZone {
    font-size: calc(13vmin);
    line-height: calc(13vmin);
  }

  .Member_Left_Win,
  .Member_Right_Win {
    display: none;
  }

  .divMainGame {
    width: 80vmin;
    height: 80vmin;
  }
}

